import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './layout/layout.component';
import { Error404Component } from './pages/error404/error404.component';

import { AuthGuard } from './pages/auth/auth.guard';

const routes: Routes = [
  {
    path: '',
    canActivate: [AuthGuard],
    component: LayoutComponent,
    children: [
      {
        path: '',
        loadChildren: () =>
          import('./pages/dashboard/dashboard.module').then(
            (m) => m.DashboardModule
          ),
      },
      {
        path: 'dashboard',
        loadChildren: () =>
          import('./pages/dashboard/dashboard.module').then(
            (m) => m.DashboardModule
          ),
      },

      {
        path: 'bikes',
        loadChildren: () =>
          import('./pages/bikes/bikes.module').then(
            (m) => m.BikesModule
          ),
      },

      {
        path: 'product-attributes',
        loadChildren: () =>
          import('./pages/product-attributes/product-attributes.module').then(
            (m) => m.ProductAttributesModule
          ),
      },
      {
        path: 'categories',
        loadChildren: () =>
          import('./pages/categories/categories.module').then(
            (m) => m.CategoriesModule
          ),
      },
      {
        path: 'products',
        loadChildren: () =>
          import('./pages/products/products.module').then(
            (m) => m.ProductsModule
          ),
      },

      {
        path: 'owners',
        loadChildren: () =>
          import('./pages/owners/owners.module').then((m) => m.OwnersModule),
      },
      {
        path: 'stores',
        loadChildren: () =>
          import('./pages/stores/stores.module').then((m) => m.StoresModule),
      },

      {
        path: 'leaders',
        loadChildren: () =>
          import('./pages/leaders/leaders.module').then((m) => m.LeadersModule),
      },
      {
        path: 'groups',
        loadChildren: () =>
          import('./pages/groups/groups.module').then((m) => m.GroupsModule),
      },
      {
        path: 'events',
        loadChildren: () =>
          import('./pages/events/events.module').then((m) => m.EventsModule),
      },

      {
        path: 'users',
        loadChildren: () =>
          import('./pages/users/users.module').then((m) => m.UsersModule),
      },

      {
        path: 'places',
        loadChildren: () =>
          import('./pages/places/places.module').then((m) => m.PlacesModule),
      },
      {
        path: 'shippings',
        loadChildren: () =>
          import('./pages/shippings/shippings.module').then(
            (m) => m.ShippingsModule
          ),
      },
      {
        path: 'shippers',
        loadChildren: () =>
          import('./pages/shippers/shippers.module').then(
            (m) => m.ShippersModule
          ),
      },
      {
        path: 'orders',
        loadChildren: () =>
          import('./pages/orders/orders.module').then((m) => m.OrdersModule),
      },
      {
        path: 'sliders',
        loadChildren: () =>
          import('./pages/sliders/sliders.module').then((m) => m.SlidersModule),
      },
      {
        path: 'ads',
        loadChildren: () =>
          import('./pages/ads/ads.module').then((m) => m.AdsModule),
      },
      {
        path: 'discounts',
        loadChildren: () =>
          import('./pages/discounts/discounts.module').then(
            (m) => m.DiscountsModule
          ),
      },
      {
        path: 'profits',
        loadChildren: () =>
          import('./pages/profits/profits.module').then((m) => m.ProfitsModule),
      },
      {
        path: 'notifications',
        loadChildren: () =>
          import('./pages/notifications/notifications.module').then(
            (m) => m.NotificationsModule
          ),
      },
    ],
  },
  {
    path: 'auth',
    loadChildren: () =>
      import('./pages/auth/auth.module').then((m) => m.AuthModule),
  },
  { path: '**', component: Error404Component },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
