import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

import { Authresonse, AuthModel } from './auth.model';
import { Subject, BehaviorSubject } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';

import { User } from './user.model';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  user = new BehaviorSubject<User>(null);
  error = new Subject<String>();

  constructor(private http: HttpClient, private router: Router) {}

  postForm(endPoint: String, form: AuthModel) {
    return this.http.post<Authresonse>(baseUrl + '/' + endPoint, form).pipe(
      tap((resData) => {
        if (resData['admin']) {
          const userData = <User>{
            token: resData.token,
            admin: resData['admin'] ?? false,
          };
          this.user.next(userData);
          this.storeUserData(userData);
        } else if (resData['owner']) {
          const userData = <User>{
            token: resData.token,
            owner: resData['owner'] ?? false,
            store: resData['store'],
          };
          this.user.next(userData);
          this.storeUserData(userData);
        } else if (resData['shipper']) {
          const userData = <User>{
            token: resData.token,
            shipper: resData['shipper'] ?? false,
          };
          this.user.next(userData);
          this.storeUserData(userData);
        }
      }),
      catchError((err) => {
        console.log(err);
        this.error.next(err.error.message);
        return this.error;
      })
    );
  }

  storeUserData(userData) {
    localStorage.setItem('user', JSON.stringify(userData));
  }

  GetUserData() {
    let userData = localStorage.getItem('user');

    return this.user.next(JSON.parse(userData));
  }

  logout() {
    this.user.next(null);
    localStorage.clear();
    this.router.navigate(['/auth/login']);
  }
}
