import {
  Component,
  OnInit,
  Output,
  EventEmitter,
  OnDestroy,
} from '@angular/core';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/pages/auth/auth.service';

@Component({
  selector: 'app-side-menu',
  templateUrl: './side-menu.component.html',
  styleUrls: ['./side-menu.component.scss'],
})
export class SideMenuComponent implements OnInit, OnDestroy {
  private userSub: Subscription;
  @Output() closeSide = new EventEmitter();

  constructor(private authService: AuthService) {}

  ngOnInit() {
    this.userSub = this.authService.user.subscribe((user) => {
      if (user) {
        if (user.admin) {
          this.items = [
            {
              isParent: 'n',
              name: 'dashboard',
              icon: 'trending_up',
              route: '/',
            },
            {
              isParent: 'n',
              name: 'Users',
              icon: 'person',
              route: '/users',
            },
            {
              isParent: 'n',
              name: 'Bikes',
              icon: 'trending_up',
              route: '/bikes',
            },
            {
              isParent: 'y',
              name: 'Products',
              children: [
                {
                  name: 'Products',
                  icon: 'trending_up',
                  route: '/products',
                },
                {
                  name: 'Categories',
                  icon: 'trending_up',
                  route: '/categories',
                },
                {
                  name: 'Product attributes',
                  icon: 'trending_up',
                  route: '/product-attributes',
                },
              ],
            },

            {
              isParent: 'y',
              name: 'Stores',
              children: [
                {
                  name: 'Stores',
                  icon: 'trending_up',
                  route: '/stores',
                },
                {
                  name: 'Owners',
                  icon: 'person',
                  route: '/owners',
                },
              ],
            },

            {
              isParent: 'y',
              name: 'Community',
              children: [
                {
                  name: 'Groups',
                  icon: 'trending_up',
                  route: '/groups',
                },
                {
                  name: 'Presidents',
                  icon: 'person',
                  route: '/leaders',
                },
                {
                  name: 'Events',
                  icon: 'trending_up',
                  route: '/events',
                },
              ],
            },

            {
              isParent: 'y',
              name: 'Shipping',
              children: [
                {
                  name: 'Orders',
                  icon: 'trending_up',
                  route: '/orders',
                },
                {
                  name: 'Shippers',
                  icon: 'trending_up',
                  route: '/shippers',
                },
                {
                  name: 'Places',
                  icon: 'trending_up',
                  route: '/places',
                },
                {
                  name: 'Shippings',
                  icon: 'trending_up',
                  route: '/shippings',
                },
              ],
            },

            {
              isParent: 'n',
              name: 'Sliders',
              icon: 'trending_up',
              route: '/sliders',
            },
            {
              isParent: 'n',
              name: 'Ads',
              icon: 'trending_up',
              route: '/ads',
            },
            {
              isParent: 'n',
              name: 'Discounts',
              icon: 'trending_up',
              route: '/discounts',
            },
            {
              isParent: 'n',
              name: 'Profits',
              icon: 'trending_up',
              route: '/profits',
            },
            {
              isParent: 'n',
              name: 'Notifications',
              icon: 'trending_up',
              route: '/notifications',
            },
          ];
        } else if (user.owner) {
          this.items = [
            {
              isParent: 'n',
              name: 'Dashboard',
              icon: 'trending_up',
              route: '/',
            },

            {
              isParent: 'n',
              name: 'Products',
              icon: 'trending_up',
              route: '/products',
            },
            {
              isParent: 'n',
              name: 'Orders',
              icon: 'trending_up',
              route: '/orders',
            },
            {
              isParent: 'n',
              name: 'Discounts',
              icon: 'trending_up',
              route: '/discounts',
            },
          ];
        } else if (user.shipper) {
          this.items = [
            {
              isParent: 'n',
              name: 'Dashboard',
              icon: 'trending_up',
              route: '/',
            },
            {
              isParent: 'n',
              name: 'Orders',
              icon: 'trending_up',
              route: '/orders',
            },
          ];
        }
      }
    });
  }
  ngOnDestroy(): void {
    this.userSub.unsubscribe();
  }

  items = [];

  closeMenu() {
    this.closeSide.emit();
  }
}
